import React, { useState } from 'react';
import './css/landing.css';
import 'animate.css';
import Slider from 'react-slick';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';
import AnimatedText from './AnimatedText';
import AnimatedTextUp from './AnimatedTextUp';
import AnimatedImage from './AnimatedImage';
import LandingImg from './images/image.jpg';
import LandingImgbg from './images/land-bg.jpg';
import slideback from './images/backland.jpg';


function Landing() {

const [slideIndex, setSlideIndex] = useState(0);


const sliderSettings = {
    slidesToShow: 1,
    // autoplay: true,
    // autoplaySpeed: 10000,
    dots: true,
    fade: true,
    beforeChange: (current, next) => {
    setSlideIndex(next);
    },
};

return (
    <section className="landing">
        <div className="landing-content">
            <Slider {...sliderSettings}>
            <div className="slide">
                {slideIndex === 0 && (
                <>
                <div className='slide-content'>
                <div className='back-overlay'>
                    <img src={slideback} alt="slide-back"/>
                    <div className='overlay'></div>
                </div>
                    <div className='text'>
                        <div className='upper-text'>
                            <div className="full-word-sm">

                                <AnimatedText text="C" delay={1200} />
                                <AnimatedText text="O" delay={1100} />
                                <AnimatedText text="N" delay={1000} />
                                <AnimatedText text="S" delay={900} />
                                <AnimatedText text="T" delay={800} />
                                <AnimatedText text="R" delay={700} />
                                <AnimatedText text="U" delay={600} />
                                <AnimatedText text="C" delay={500} />
                                <AnimatedText text="T" delay={400} />
                                <AnimatedText text="I" delay={300} />
                                <AnimatedText text="O" delay={200} />
                                <AnimatedText text="N" delay={100} />

                            </div>

                            <div className="full-word-sm">

                                <AnimatedText text="C" delay={700} />
                                <AnimatedText text="O" delay={600} />
                                <AnimatedText text="M" delay={500} />
                                <AnimatedText text="P" delay={400} />
                                <AnimatedText text="A" delay={300} />
                                <AnimatedText text="N" delay={200} />
                                <AnimatedText text="Y" delay={100} />

                            </div>


                            <div className="full-word-sm">

                                <AnimatedText text="E" delay={600} />
                                <AnimatedText text="A" delay={500} />
                                <AnimatedText text="A" delay={400} />
                                <AnimatedText text="M" delay={300} />
                                <AnimatedText text="A" delay={200} />
                                <AnimatedText text="R" delay={100} />

                            </div>
                        </div>


                        <div className='lower-text'>
                            <div className="full-word-lg">

                                <AnimatedTextUp text="E" delay={600} />
                                <AnimatedTextUp text="A" delay={500} />
                                <AnimatedTextUp text="A" delay={400} />
                                <AnimatedTextUp text="M" delay={300} />
                                <AnimatedTextUp text="A" delay={200} />
                                <AnimatedTextUp text="R" delay={100} />

                            </div>

                            <div className="full-word-lg">

                                <AnimatedTextUp text="C" delay={700} />
                                <AnimatedTextUp text="O" delay={600} />
                                <AnimatedTextUp text="M" delay={500} />
                                <AnimatedTextUp text="P" delay={400} />
                                <AnimatedTextUp text="A" delay={300} />
                                <AnimatedTextUp text="N" delay={200} />
                                <AnimatedTextUp text="Y" delay={100} />

                            </div>

                        </div>

                        <a href="/">
                            VIEW PROJECTS
                        </a>
                        

                    </div>
                    
                    <AnimatedImage classname="small-img" width="350px" height="350px" imageSrc={LandingImg} delay={200} />
                    <AnimatedImage classname="large-img" width="650px" height="650px" imageSrc={LandingImgbg} delay={300} />
                    </div>
                </>
                )}
                </div>


                <div className="slide">
                {slideIndex === 1 && (
                <>
                <div className='slide-content'>
                <div className='back-overlay'>
                    <img src={slideback} alt="slide-back"/>
                    <div className='overlay'></div>
                </div>
                    <div className='text'>
                        <div className='upper-text'>
                            <div className="full-word-sm">

                                <AnimatedText text="C" delay={1200} />
                                <AnimatedText text="O" delay={1100} />
                                <AnimatedText text="N" delay={1000} />
                                <AnimatedText text="S" delay={900} />
                                <AnimatedText text="T" delay={800} />
                                <AnimatedText text="R" delay={700} />
                                <AnimatedText text="U" delay={600} />
                                <AnimatedText text="C" delay={500} />
                                <AnimatedText text="T" delay={400} />
                                <AnimatedText text="I" delay={300} />
                                <AnimatedText text="O" delay={200} />
                                <AnimatedText text="N" delay={100} />

                            </div>

                            <div className="full-word-sm">

                                <AnimatedText text="C" delay={700} />
                                <AnimatedText text="O" delay={600} />
                                <AnimatedText text="M" delay={500} />
                                <AnimatedText text="P" delay={400} />
                                <AnimatedText text="A" delay={300} />
                                <AnimatedText text="N" delay={200} />
                                <AnimatedText text="Y" delay={100} />

                            </div>


                            <div className="full-word-sm">

                                <AnimatedText text="E" delay={600} />
                                <AnimatedText text="A" delay={500} />
                                <AnimatedText text="A" delay={400} />
                                <AnimatedText text="M" delay={300} />
                                <AnimatedText text="A" delay={200} />
                                <AnimatedText text="R" delay={100} />

                            </div>
                        </div>


                        <div className='lower-text'>
                            <div className="full-word-lg">

                                <AnimatedTextUp text="E" delay={600} />
                                <AnimatedTextUp text="A" delay={500} />
                                <AnimatedTextUp text="A" delay={400} />
                                <AnimatedTextUp text="M" delay={300} />
                                <AnimatedTextUp text="A" delay={200} />
                                <AnimatedTextUp text="R" delay={100} />

                            </div>

                            <div className="full-word-lg">

                                <AnimatedTextUp text="C" delay={700} />
                                <AnimatedTextUp text="O" delay={600} />
                                <AnimatedTextUp text="M" delay={500} />
                                <AnimatedTextUp text="P" delay={400} />
                                <AnimatedTextUp text="A" delay={300} />
                                <AnimatedTextUp text="N" delay={200} />
                                <AnimatedTextUp text="Y" delay={100} />

                            </div>

                        </div>

                        <a href="/">
                            VIEW PROJECTS
                        </a>
                        

                    </div>
                    
                    <AnimatedImage classname="small-img" width="350px" height="350px" imageSrc={LandingImg} delay={200} />
                    <AnimatedImage classname="large-img" width="650px" height="650px" imageSrc={LandingImgbg} delay={200} />
                    </div>
                </>
                )}
                </div>


            </Slider>
        </div>
    </section>
);
}

export default Landing;
