import './css/ourprojects.css';
import projectone from './images/project-1.jpg';
import projecttwo from './images/project-2.jpg';
import projectthree from './images/project-3.jpg';
import projectfour from './images/project-4.jpg';


function OurProjects(){
    return(
        <>

            <section className='recent-projects'>
                <div className='container'>
                    <div className='row'>
                        <div className='col-lg-12 col-md-12 col-sm-12 col-xs-12'>
                            <div className="section-header">
                                <h2>PROMOTIONS</h2>
                                <h1>Recent Projects</h1>
                            </div>
                        </div>

                        <div className='col-lg-3 col-md-3 col-sm-6 col-xs-12'>
                            <div className='recent-project'>
                                <img src={projectone} alt="project"/>

                                <div className='info'>
                                    <span className='tag'> PROJECT </span>

                                    <div className='more-info'>
                                        <h2> BUILDINGS </h2>

                                        <a href="/"><i class="las la-plus"></i></a>
                                    </div>
                                </div>
                            </div>
                        </div>


                        <div className='col-lg-3 col-md-3 col-sm-6 col-xs-12'>
                            <div className='recent-project'>
                                <img src={projecttwo} alt="project"/>

                                <div className='info'>
                                    <span className='tag'> RENOVATE </span>

                                    <div className='more-info'>
                                        <h2> PAINTAINGS </h2>

                                        <a href="/"><i class="las la-plus"></i></a>
                                    </div>
                                </div>
                            </div>
                        </div>


                        <div className='col-lg-3 col-md-3 col-sm-6 col-xs-12'>
                            <div className='recent-project'>
                                <img src={projectthree} alt="project"/>

                                <div className='info'>
                                    <span className='tag'> RENOVATE </span>

                                    <div className='more-info'>
                                        <h2> ELECTRICITY </h2>

                                        <a href="/"><i class="las la-plus"></i></a>
                                    </div>
                                </div>
                            </div>
                        </div>


                        <div className='col-lg-3 col-md-3 col-sm-6 col-xs-12'>
                            <div className='recent-project'>
                                <img src={projectfour} alt="project"/>

                                <div className='info'>
                                    <span className='tag'> RENOVATE </span>

                                    <div className='more-info'>
                                        <h2> MACHINE </h2>

                                        <a href="/"><i class="las la-plus"></i></a>
                                    </div>
                                </div>
                            </div>
                        </div>


                    </div>
                </div>
            </section>

        </>
    )
}

export default OurProjects;