import React from 'react';
import { useSpring, animated } from '@react-spring/web';

function AnimatedImg({ imageSrc, delay,width,height,classname }) {
const props = useSpring({
    to: {clipPath:" circle(100%)"  },
    from: {clipPath:" circle(1%)" },
    reset:true,
    delay,
});

return (
    <animated.img className={classname} style={props} src={imageSrc} alt="animated" />
);
}

export default AnimatedImg;
