import React, { useState } from 'react';
import './css/gallery.css';
import gallery1 from './images/project-1.jpg';
import gallery2 from './images/project-2.jpg';
import gallery3 from './images/project-3.jpg';
import gallery4 from './images/project-4.jpg';
import gallery5 from './images/gallery.jpg';
import gallery6 from './images/footer.jpg';
import gallery7 from './images/contact-back.jpg';

import Stats from './Stats';

const Gallery = () => {
  const [selectedImg, setSelectedImg] = useState(null);

  const images = [
    gallery1,
    gallery2,
    gallery3,
    gallery4,
    gallery5,
    gallery6,
    gallery7
  ];

  const handleImageClick = (imgSrc) => {
    setSelectedImg(imgSrc);
  }

  const handleCloseClick = () => {
    setSelectedImg(null);
  }

  return (
    <>

      <div className='page-header'>
            <div className='overlay'></div>
            <h1>GALLERY</h1>
        </div>



    <Stats />


<div className='container'>
  <div className='row'>
    <div className="gallery">
      {images.map((imgSrc, index) => (
        <div className='col-lg-3 col-md-4 col-sm-6 col-xs-12'>
          <img 
            key={index} 
            src={imgSrc} 
            alt={`Image ${index}`} 
            className="gallery-img"
            onClick={() => handleImageClick(imgSrc)}
          />
        </div>
      ))}
      {selectedImg && (

        
        

       
            <div className="modal">
              <span className="close" onClick={handleCloseClick}>&times;</span>
              <img src={selectedImg} alt="Selected Image" className="modal-img" />
            </div>
          
        

      )}
    </div>

  </div>
</div>
    </>

  );
};

export default Gallery;
